import React, { Component } from 'react';
import '../../assets/css/App.css';
import '../../assets/css/main.css';
import '../../assets/css/color_skins.css';
import '../../assets/css/vendor/bootstrap/css/bootstrap.min.css';
import '../../assets/css/vendor/font-awesome/css/font-awesome.min.css';
import '../../assets/css/vendor/chartist/css/chartist.min.css';
import '../../assets/css/vendor/chartist-plugin-tooltip/chartist-plugin-tooltip.css';
import '../../assets/css/vendor/toastr/toastr.min.css';
import { Link } from 'react-router-dom';

class OTPLogin extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showBox : false
    }
  }
  componentDidMount() {
    document.body.classList.add('theme-orange');
  }
  showInput(){
      this.setState({
        showBox: !this.state.showBox
      })
  }
  render() {
    return (
      <div id="wrapper">
        <div className="vertical-align-wrap">
          <div className="vertical-align-middle auth-main">
            <div className="auth-box">
              <div className="top">
                <img src={require("../../assets/images/logo-white.svg")} alt="Lucid" />
              </div>
              <div className="card">
                <div className="header">
                  <p className="lead">Login to mobile number</p>
                </div>
                <div className="body">
                  <div className="form-auth-small">
                    <div className="form-group">
                      <label for="signin-email" className="control-label sr-only">Mobile</label>
                      <input type="text" className="form-control" id="signin-email" placeholder="Enter Mobile" />
                    </div>
                    {this.state.showBox ? <div className="form-group">
                      <label for="signin-password" className="control-label sr-only">Password</label>
                      <input type="text" className="form-control" id="signin-password" placeholder="Enter OTP" />
                    </div> : null }
                    <button className="btn btn-primary btn-lg btn-block" onClick={()=>this.showInput()}>SUBMIT</button>
                    <div className="bottom">
                      <span className="helper-text m-b-10">OR</span>
                      <Link to="/login"><span>Login With Your Credencials</span></Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default OTPLogin;
