import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom'
import App from './containers/root/App';
import Profile from './containers/user/Profile';
import Header from './components/common/Header';
import LeftPanel from './components/common/LeftPanel';
import Team from './containers/team/Team';
import Chat from './containers/chat/ChatList';
import Task from './containers/task/Task';
import Works from './containers/works/Works';
import Events from './containers/events/Events';
import TeamDetails from './containers/team/TeamDetails';
import Login from './containers/user/Login';
import OTPLogin from './containers/user/OTPLogin';
import Actions from './containers/actions/Actions';
import ActionDetails from './containers/actions/ActionDetails';
import WorkDetails from './containers/works/WorkDetails';
// import TopPanel from './components/common/TopPanel'

const Routes = () => (
    <div>
        {localStorage.getItem('isLogin') === 'true' ? <Header /> : ''}
        {localStorage.getItem('isLogin') === 'true' ? <LeftPanel /> : ''}
        <Switch>
            <Route exact path="/" component={App} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/otp-login" component={OTPLogin} />
            <Route exact path="/chat" component={Chat} />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/team" component={Team} />
            <Route exact path="/team-details" component={TeamDetails} />
            <Route exact path="/task" component={Task} />
            <Route exact path="/works" component={Works} />
            <Route exact path="/events" component={Events} />
            <Route exact path="/actions" component={Actions} />
            <Route exact path="/action-details" component={ActionDetails} />
            <Route exact path="/work-details" component={WorkDetails} />
        </Switch>
    </div>
)

export default Routes