import React, { Component } from 'react';
import '../../assets/css/App.css';
import '../../assets/css/main.css';
import '../../assets/css/color_skins.css';
import '../../assets/css/vendor/bootstrap/css/bootstrap.min.css';
import '../../assets/css/vendor/font-awesome/css/font-awesome.min.css';
import '../../assets/css/vendor/chartist/css/chartist.min.css';
import '../../assets/css/vendor/chartist-plugin-tooltip/chartist-plugin-tooltip.css';
import '../../assets/css/vendor/toastr/toastr.min.css';
import { Link } from 'react-router-dom';

class Login extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
  }
  componentDidMount() {
    document.body.classList.add('theme-orange');
  }
  doLogin(){
    localStorage.setItem('isLogin', true);
    this.props.history.push('/');
  }
  render() {
    return (
      <div id="wrapper">
        <div className="vertical-align-wrap">
          <div className="vertical-align-middle auth-main">
            <div className="auth-box">
              <div className="top">
                <img src={require("../../assets/images/logo-white.svg")} alt="Lucid" />
              </div>
              <div className="card">
                <div className="header">
                  <p className="lead">Login to your account</p>
                </div>
                <div className="body">
                  <div className="form-auth-small">
                    <div className="form-group">
                      <label for="signin-email" className="control-label sr-only">Email</label>
                      <input type="email" className="form-control" id="signin-email" placeholder="Enter Email / Username" />
                    </div>
                    <div className="form-group">
                      <label for="signin-password" className="control-label sr-only">Password</label>
                      <input type="password" className="form-control" id="signin-password"  placeholder="Enter Password" />
                    </div>
                    <div className="form-group clearfix">
                      <label className="fancy-checkbox element-left">
                        <input type="checkbox" />
                        <span>Remember me</span>
                      </label>
                    </div>
                    <button className="btn btn-primary btn-lg btn-block" onClick={()=>this.doLogin()}>LOGIN</button>
                    <div className="bottom">
                      <span className="helper-text m-b-10">OR</span>
                      <Link to="/otp-login"><span>Login With OTP</span></Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Login;
