import React, { Component, useState } from 'react';
import '../../assets/css/App.css';
import '../../assets/css/main.css';
import '../../assets/css/color_skins.css';
import '../../assets/css/vendor/bootstrap/css/bootstrap.min.css';
import '../../assets/css/vendor/font-awesome/css/font-awesome.min.css';
import '../../assets/css/vendor/chartist/css/chartist.min.css';
import '../../assets/css/vendor/chartist-plugin-tooltip/chartist-plugin-tooltip.css';
import '../../assets/css/vendor/toastr/toastr.min.css';
import { Link } from 'react-router-dom';

class LeftPanel extends Component {
  constructor(props) {
    super(props)
    this.state = {
      panelClass: 'hide'
    }
  }

  hidePanel() {
    this.setState({ panelClass: this.state.panelClass == 'show' ? 'hide' : 'show' })
  }
  removeClass(){
     document.body.classList.remove('offcanvas-active');
  }
  logout(){
    localStorage.setItem('isLogin', false);
    this.props.history.push('/login');
  }
  render() {
    return (
      <div>
        <div id="left-sidebar" className="sidebar">
          <div className="sidebar-scroll">
            <div className="user-account">
              <img src={require("../../assets/images/user.png")} className="rounded-circle user-photo" alt="User Profile Picture" />
              <div className={`dropdown ${this.state.panelClass}`}>
                <span>Welcome,</span>
                {/* <Link className="has-arrow dropdown-toggle user-name" to="/profile"><strong>Jessica Doe</strong></Link> */}
                <a href="javascript:void(0);" onClick={() => this.hidePanel()} className="dropdown-toggle user-name" data-toggle="dropdown"><strong>Jessica Doe</strong></a>
                <ul className={`dropdown-menu dropdown-menu-right account animated flipInY ${this.state.panelClass}`} style={{ position: "absolute", top: "100%", left: "0px" }}>
                  <Link to="/profile" onClick={() => this.hidePanel()}><i className="icon-user"></i><strong>My Profile</strong></Link>
                  {/* <li><a href="page-profile2.html"><i className="icon-user"></i>My Profile</a></li> */}
                  <li><a href="app-inbox.html"><i className="icon-envelope-open"></i>Messages</a></li>
                  <li><a href="javascript:void(0);"><i className="icon-settings"></i>Settings</a></li>
                  <li className="divider"></li>
                  <li><a href="login" onClick={()=>this.logout()}><i className="icon-power"></i>Logout</a></li>
                </ul>
              </div>
              <hr></hr>
              <div className="row">
                <div className="col-4">
                  <h6>5+</h6>
                  <small>Task</small>
                </div>
                <div className="col-4">
                  <h6>400+</h6>
                  <small>Team</small>
                </div>
                <div className="col-4">
                  <h6>80+</h6>
                  <small>Leaves</small>
                </div>
              </div>
            </div>
            <hr></hr>
            <div className="tab-content p-l-0 p-r-0">
              <div className="tab-pane animated fadeIn active" id="hr_menu">
                <nav className="sidebar-nav">
                  <ul className="main-menu metismenu">
                    <li>
                      <Link className="has-arrow active" to="/" onClick={()=>this.removeClass()}><i className="icon-speedometer"></i>Dashboard</Link>
                    </li>
                    <li>
                      <Link className="has-arrow" to="/chat" onClick={()=>this.removeClass()}><i className="icon-bubbles"></i>Chat</Link>
                    </li>
                    <li>
                      <Link className="has-arrow" to="/task" onClick={()=>this.removeClass()}><i className="icon-users"></i>Task</Link>
                    </li>
                    <li>
                      <Link className="has-arrow" to="/team" onClick={()=>this.removeClass()}><i className="icon-users"></i>Team</Link>
                    </li>
                    <li>
                      <Link className="has-arrow" to="/actions" onClick={()=>this.removeClass()}><i className="icon-users"></i>Actions</Link>
                    </li>
                    <li>
                      <Link className="has-arrow" to="/works" onClick={()=>this.removeClass()}><i className="icon-users"></i>Works</Link>
                    </li>
                    <li>
                      <a href="login" className="has-arrow" onClick={()=>this.logout()}><i className="icon-login"></i><span>Logout</span></a>
                    </li>
                  </ul>
                </nav>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default LeftPanel;
